@charset "utf-8";

fieldset{
  width: 400px;
  margin: 0 auto;
  border: none;
}
legend{
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  color: var(--main_color2);
  margin-bottom: 20px;
}

label{
  display: block;
  margin:10px 0px;
}
input{
  width: 100%;
  padding: 15px 0px;
  background:var(--main_color1);
  border: none;
}
input::placeholder{
  color:var(--base_color2);
  text-indent: 10px;
}

.submit_btn{
  width: 100%;
  padding: 15px 0px;
  margin-top: 20px;
  background-color: var(--main_color2);
  color: var(--base_color2);
  border: none;
  font-size: 16px;
  font-weight: bold;
}
.product_list li{
  line-height: 30px;
}
.product_list img{
  width: 100%
}

.btn{
  color: var(--base_color2);
  margin-top: 15px;
}

